<template>
  <div>
    <form>
      <AppFieldset>
        <Alert>      ,        !<br><br>    ,   -        !</Alert>
        <AppInput name="uploaded_files" type="hidden" />
        <AppSelect
            name="type"
            rules="required"
            label=" "
            :options="typeOptions"
            @selectFromSelect="GetTypeSelected"
        />
        <AppInput name="last_name" rules="required" label="" />
        <AppInput name="name" rules="required" label="" />
        <AppInput name="second_name" label="" />
        <AppInput name="snils" rules="required" label="" v-maska="'###-###-###-##'"/>
        <AppInput type="tel" name="phone" rules="required" label="" />
        <AppInput type="email" name="email" rules="required|email" label=" " />
        <AppSelect
            name="account"
            rules="required"
            label="  "
            :options="accountOptions"
            infomessage="      "
            @selectFromSelect="GetSelectSelected"
        />
        <AppInput type="text" name="address" rules="required" label=""/>
        <template v-if="currentType === 1">
          <AppInput type="textarea" name="comment" rules="required" label=" " />
        </template>
        <template v-if="currentType === 2">
          <AppDateInterval
              label=" "
              name="period"
              rules="required"
              @updateDate="dateUpdateHandler"
          />
        </template>
        <template v-if="currentType === 3">
          <AppInput type="text" name="device_name" rules="required" label="   " />
          <AppInput type="number" name="device_indication" rules="required" label=""/>
        </template>
        <template v-if="currentType === 4">
          <AppInput type="textarea" name="derection_target" rules="required" label="  " />
        </template>
        <template v-if="currentType === 5">
          <AppInput type="text" name="org_name" rules="required" label="  " />
          <AppDatePicker name="verification_end" rules="required" label="   " />
          <AppSelect
              name="verification"
              rules="required"
              label=" "
              :options="verificationTypes"
              infomessage="    "
          />
        </template>
        <template v-if="currentType === 6">
          <AppDateInterval
              label="     "
              name="stop"
              rules="required"
              @updateDate="dateUpdateHandler"
          />
          <AppInput type="number" name="device_current_indication" rules="required" label="  (3)"/>
        </template>
        <template v-if="currentType === 7">
          <AppSelect
              name="sealing"
              rules="required"
              label=" "
              :options="reasonForSealingTypes"
              infomessage="   "
              @selectFromSelect="GetSealingTypes"
          />
          <AppInput v-if="sealingType === 5" type="text" name="reason_sealing" rules="required" label=" "/>
          <AppSelect
              name="counter_place"
              rules="required"
              label="  "
              :options="counterPlaceTypes"
              infomessage="    "
          />
        </template>
        <template v-if="currentType === 8">
          <AppDatePicker name="defect_date" rules="required" label="    ( )" />
          <AppSelect
              name="defect_type"
              rules="required"
              label="  "
              :options="defectTypes"
              infomessage="    "
              @selectFromSelect="GetDefectType"
          />
          <AppInput v-if="defectType === 4" type="textarea" name="defect_type_comment" rules="required" label="   "/>
        </template>
        <template v-if="currentType === 9">
          <AppInput type="number" name="debt_amount" rules="required" label="     ()"/>
          <AppDatePicker class="sm:tw-col-span-3" name="amortization_date" rules="required" label="  " />
          <AppDatePicker class="sm:tw-col-span-3" name="payment_date" rules="required" label="         " />
          <AppDateInterval
              label="   "
              name="connection"
              rules="required"
              @updateDate="dateUpdateHandler"
          />
        </template>
        <template v-if="currentType === 10">
          <AppInput type="number" name="debt_amount" rules="required" label="     ()"/>
          <AppSelect
              name="counterPlace"
              rules="required"
              label=" "
              :options="applicationSubjectTypes"
              infomessage="    "
          />
          <AppSelect
              name="debt_percentage"
              rules="required"
              label="   1  (    ), % :"
              :options="debtPercentageTypes"
              infomessage="   "
          />
          <AppInput type="number" name="debt_months" rules="required" label="      "/>
        </template>
        <AppFile :filename="files" name="files" label="" accept=".jpg,.jpeg,.png,.doc,.docx,.pdf" multiple />
        <AppCheck name="agree" label=" ,    " rules="required" value="yes"/>
        <AppCheck name="emailme" label="  -        " value="yes"/>
      </AppFieldset>
      <br>
      <hr>
      <ComplexFormToolbar @submit="onSubmit" @showModal="submitModal = true" @handleValidForm="handleValidForm"/>
    </form>
    <AppModal v-model="submitModal">
      <ComplexFormModal @submit="onSubmit" @hideModal="submitModal = false" />
    </AppModal>
    <AppLoading :showing="loading" />
  </div>
</template>

<script>
import useComplexForm from '@/compositions/useComplexForm';
import Alert from '@/components/Alert';
import { useStore } from 'vuex';
import AppDatePicker from "@/core/AppDatePicker.vue";
import AppLabel from "@/core/AppLabel.vue";
import AppInput from "@/core/AppInput.vue";
import AppSelect from "@/core/AppSelect.vue";
import store from "@/store";
import AppDateInterval from "@/core/AppDateInterval.vue";

export default {
  data(){
    return {
      accountOptions: [],
      typeOptions: [],
      verificationTypes: [],
      reasonForSealingTypes: [],
      counterPlaceTypes: [],
      defectTypes: [],
      applicationSubjectTypes: [],
      debtPercentageTypes: [],
      currentType: '',
      sealingType: '',
      defectType: '',
    }
  },
  emits: ['selectFromSelect'],
  async created() {
    try {
      [
        this.accountOptions,
        this.typeOptions,
        this.verificationTypes,
        this.reasonForSealingTypes,
        this.counterPlaceTypes,
        this.defectTypes,
        this.applicationSubjectTypes,
        this.debtPercentageTypes
      ] = await Promise.all(
          [
            store.dispatch('complexForm/getAccounts'),
            store.dispatch('complexForm/getRequestTypes'),
            store.dispatch('complexForm/getVerificationTypes'),
            store.dispatch('complexForm/getReasonForSealingTypes'),
            store.dispatch('complexForm/getCounterPlaceTypes'),
            store.dispatch('complexForm/getDefectTypes'),
            store.dispatch('complexForm/getSubjectApplicationTypes'),
            store.dispatch('complexForm/getDebtPercentageTypes')
      ])

    } catch (e){
      console.error(e)
    }
  },
  methods: {
      GetSelectSelected(value) {
        this.handleAddress(value);
      },
      GetTypeSelected(value){
        this.currentType = +value
      },
      GetSealingTypes(value){
        this.sealingType = +value
      },
      GetDefectType(value){
        this.defectType = +value
      },
      dateUpdateHandler(name, value) {
        this.handleDateInterval(name, value)
      }
  },
  setup() {
    const initialValues = getInitVals();
    const { onSubmit, submitModal, loading, handleValidForm, handleAddress, handleDateInterval} = useComplexForm(initialValues, { social: true });
    const files = [];

    return {
        onSubmit,
        submitModal,
        loading,
        handleValidForm,
        handleAddress,
        files,
        handleDateInterval
    };
  },
  components: {
    AppDateInterval,
    AppSelect,
    AppInput,
    AppLabel,
    AppDatePicker,
    Alert,
    ComplexFormLayout: require('@/components/ComplexFormLayout').default,
    ComplexFormToolbar: require('@/components/ComplexFormToolbar').default,
    ComplexFormModal: require('@/components/ComplexFormModal').default,
  },
};

function getInitVals() {



  return {
    comment: '',
    files: null,
    agree: "",
    emailme: ""

  };
}
</script>
