  props: {
    modelValue: {
      default: false,
      type: Boolean,
    },
  },
  emits: ['update:modelValue'],
  watch: {
    modelValue(val) {
      if(val) document.body.style.overflow = 'hidden';
      else document.body.style.overflow = '';
    }
  }
